import React, {useEffect} from 'react';
import {useLocation} from "react-router-dom";
import {config} from '../consts/config';
import md5 from 'js-md5'

export const Home = () => {
  const code = useLocation().search;
  useEffect(() => {
    const form = new FormData();
    form.append('client_id', config.clientId);
    form.append('client_secret', config.clientSecret);
    form.append('code', code);
    form.append('verify', md5([config.clientId, config.clientSecret, code].join(':')));

    fetch('https://joinposter.com/api/auth/manage', {
      method: 'POST',
      body: form
    }).then(result => {
      console.log(md5([config.clientId, config.clientSecret, code].join(':')));
    })
  }, [code])
  return (
      <div>
       <p>TEST1</p>
      </div>
  )
}
